//------------------------------------------------------------
// コラム＆ニュースTOP
//------------------------------------------------------------

// 記事一覧はガイドと同じです

//------------------------------------------------------------
// コラム＆ニュース記事詳細
//------------------------------------------------------------

// アイキャッチ画像のstyleはtitle.scssの中です

.news-page {
	p {
		@include margin(vw(20) null vw(20) null);
		&:first-of-type {
			margin-top: 0;
		}
	}
}

// 次ページの記事紹介
//------------------------------------------------------------

.next-content {
	position: relative;
	@include margin(vw(30) null vw(30) null);
	text-align: center;
}

// ニュース提供元
//------------------------------------------------------------

.news-supply-link {
	text-align: right;
	@include margin(vw(30) null vw(30) null);
	img {
		width: vw(72);
		vertical-align: baseline;
	}
}

// 新着記事一覧
//------------------------------------------------------------

// 基本ベースは相場のstyle.scssのcar-thumb-list-areaです。追加要素のみ記載してます
.news-list-area {
	.txt-rank {
		font-size: vw(12);
	}
	.news-thumb-memo {
		margin-bottom: vw(5);
	}
	&.car-thumb-area {
		.car-thumb-list {
			.car-thumb-img {
				height: 19.5vw;
				padding: 0;
			}
			.txt-rank {
				width: 64%;
			}
		}
	}
}
