//------------------------------------------------------------
// 相場TOP
//------------------------------------------------------------

// キーワード検索の入力パーツ
//------------------------------------------------------------

input[type="text"].input-search-keyword {
	width: 75%;
	height: vw(36);
	margin-right: vw(5);
	vertical-align: top;
}

//=========================
// メーカーロゴならぶブロック
//=========================

.maker-search {
	&-list {
		display: block;
		width: 100%;
		border: vw(1) solid colors(link);
		border-bottom: none;
		font-size: 0;
		box-sizing: border-box;
		&:last-child {
			border-bottom: vw(1) solid colors(link);
		}
		.advanced-element & {
			&:first-child {
				border-top: none;
			}
		}
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
			&:visited .ico-arrow {
				color: colors(visited);
			}
		}
		figure {
			position: relative;
			padding: vw(5) vw(20) vw(5) vw(5);
			box-sizing: border-box;
		}
		figcaption {
			width: calc(100% - #{vw(71)});
			@include inline-block;
			font-size: vw(14);
			text-decoration: underline;
			vertical-align: middle;
		}
		.ico-arrow {
			position: absolute;
			top: 50%;
			right: vw(5);
			color: colors(link);
			font-size: vw(14);
			@include translate(0, -50%);
		}
	}
	&-img {
		@include inline-block;
		position: relative;
		width: vw(66);
		height: vw(42);
		margin-right: vw(5);
		vertical-align: middle;
		img {
			position: relative;
			top: 50%;
			left: 50%;
			max-height: 100%;
			@include translate(-50%,-50%);
		}
	}
}

//------------------------------------------------------------
// メーカー / キーワード検索
//------------------------------------------------------------

//=========================
// 車の写真ならぶブロック（ランキングやメーカーページなど）
//=========================

.car-thumb {
	&-area {
		margin-bottom: vw(10);
	}
	&-list {
		border: vw(1) solid colors(link);
		border-bottom: none;
		&:last-child {
			border-bottom: vw(1) solid colors(link);
		}
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
			&:visited .ico-arrow {
				color: colors(visited);
			}
		}
		figure {
			@include flex-block;
			position: relative;
			-webkit-align-items: center;
			align-items: center;
			width: 100%;
			padding: vw(10) vw(20) vw(10) vw(5);
			box-sizing: border-box;
			flex-wrap: wrap;
			.car-thumb-img {
				@include inline-block;
				position: relative;
				width: vw(90);
				min-width: vw(90);
				height: vw(55);
				margin-right: vw(5);
				padding: vw(5);
				border: vw(1) solid colors(link);
				overflow: hidden;
				box-sizing: border-box;
				img {
					position: relative;
					top: 50%;
					left: 50%;
					max-height: 100%;
					@include translate(-50%,-50%);
				}
			}
			.ico-rank {
				@include inline-block;
				width: vw(27);
				height: vw(38);
				margin-right: 5px;
			}
			.txt-rank {
				font-weight: 700;
				&.multiple {
					width: calc(100% - #{vw(117)});
					.car-thumb-make-name {
						display: block;
						line-height: 1.2;
					}
					.car-thumb-price {
						display: block;
						margin-top: vw(5);
						line-height: 1;
					}
				}
			}
			.ico-arrow {
				position: absolute;
				top: 50%;
				right: vw(5);
				color: colors(link);
				@include translate(0, -50%);
			}
		}
	}
}

//------------------------------------------------------------
// 車種詳細
//------------------------------------------------------------

//=========================
// 口コミリスト（相場と査定会社で利用）
//=========================

.review {
	&-list-area {
		a {
			display: block;
			color: colors(basetxt);
			text-decoration: none;
			box-sizing: border-box;
			&:hover {
				@include block-link;
			}
			.review-list-area-inner {
				border: vw(1) solid colors(col-lightgray);
				border-bottom: none;
				color: colors(basetxt);
				&.pickup-review {
					padding: vw(8);
					border: vw(2) solid colors(col-yellow);
				}
			}
			&:last-child .review-list-area-inner {
				border-bottom: vw(1) solid colors(col-lightgray);
			}
		}
		&-inner {
			padding: vw(10);
			border: vw(1) solid colors(col-lightgray);
			border-bottom: none;
			font-size: vw(12);
			&:last-child {
				border-bottom: vw(1) solid colors(col-lightgray);
			}
			&.pickup-review {
				padding: vw(8);
				border: vw(2) solid colors(col-yellow);
			}
		}
	}
	// 口コミサブ情報ブロック
	//------------------------------------------------------------
	&-supplement-info-block {
		// .mv-review-supplement-info-blockにextendしているので、編集する際は継承先にも注意する
		display: table;
		width: 100%;
		margin-bottom: vw(8);
		padding-bottom: vw(8);
		border-bottom: vw(1) dotted colors(col-lightgray);
		font-size: vw(10);
		line-height: 1;
	}
	&-pickup-ico-box {
		@include table-cell;
		width: vw(120);
		@include padding(vw(3) vw(2) vw(2));
		background-color: colors(col-yellow);
		font-size: vw(12);
		font-weight: 700;
		text-align: center;
		text-shadow: vw(1) vw(1) 0px colors(col-white);
		box-sizing: border-box;
	}
	&-user-name-box {
		// .mv-review-name-boxにextendしているので、編集する際は継承先にも注意する
		@include table-cell;
		width: vw(45);
		padding-left: vw(5);
		padding-right: vw(5);
		&:first-child {
			padding-left: 0px;
		}
	}
	&-days-box {
		// .mv-review-days-boxにextendしているので、編集する際は継承先にも注意する
		@include table-cell;
		text-align: right;
	}
	// 口コミメイン情報ブロック
	//------------------------------------------------------------
	&-main-info-block {
		// .mv-review-main-info-block, .mv-shop-outline-blockにextendしているので、編集する際は継承先にも注意する
		@include flex-block;
		-webkit-flex-wrap: wrap;
		flex-wrap: wrap;
		width: 100%;
	}
	&-spec {
		&-box {
			// .mv-review-sell-spec-box, .mv-shop-outline-score-boxにextendしているので、編集する際は継承先にも注意する
			display: block;
			clear: both;
			width: 100%;
			box-sizing: border-box;
		}
		&-list {
			// .mv-review-sell-spec-list, .mv-shop-outline-listにextendしているので、編集する際は継承先にも注意する
			display: table;
			width: 100%;
			@include margin(vw(5) null vw(5) null);
			&-ttl {
				// .mv-review-sell-spec-list-ttlにextendしているので、編集する際は継承先にも注意する
				@include table-cell;
				width: vw(65);
				padding-right: vw(5);
				color: colors(col-blue);
				font-weight: 700;
			}
			&-body {
				// .mv-review-sell-spec-list-bodyにextendしているので、編集する際は継承先にも注意する
				@include table-cell;
				word-wrap: break-word;
				overflow-wrap : break-word;
				.review-star {
					@extend %review-star;
					position: relative;
					top: vw(1.6);
					margin-right: vw(3);
					font-size: vw(16);
					letter-spacing: vw(-3);
				}
				a {
					&:link {
						color: colors(link);
						text-decoration: underline;
					}
					&:visited {
						color: colors(visited);
					}
					&.link-base-col {
						color: colors(basetxt);
						text-decoration: none;
						@include transition(0)
						&:hover {
							background-color: transparent;
							box-shadow: none;
						}
					}
				}
			}
		}
	}
	&-sell-price {
		&-box {
			// .mv-review-sell-price-boxにextendしているので、編集する際は継承先にも注意する
			@include inline-block;
			width: 40%;
			padding-right: vw(10);
			box-sizing: border-box;
			// 一括査定ご利用ユーザー様の声に使用
			&.uservoice {
				width: 60%;
			}
		}
		&-list {
			&-ttl {
				// .mv-review-sell-price-list-ttlにextendしているので、編集する際は継承先にも注意する
				color: colors(col-blue);
				font-size: vw(12);
				font-weight: 700;
				line-height: 1;
			}
			&-body {
				// .mv-review-sell-price-list-bodyにextendしているので、編集する際は継承先にも注意する
				color: colors(col-red);
				font-size: vw(10);
				font-weight: 700;
				line-height: 1.25;
			}
		}
	}
	&-difference {
		&-box {
			@include inline-block;
			width: auto;
			box-sizing: border-box;
		}
		&-list {
			display: flex;
			align-items: baseline;
			&-ttl {
				width: vw(65);
				padding-right: vw(5);
				color: colors(col-blue);
				font-size: vw(12);
				font-weight: 700;
				line-height: 1;
			}
			&-body {
				color: colors(col-red);
				font-size: vw(10);
				font-weight: 700;
				line-height: 1.25;
				&-supplement {
					margin-left: 4px;
					color: colors(basetxt);
					font-weight: normal;
				}
			}
		}
	}
	&-user-eval {
		&-box {
			@extend .review-spec-box;
			width: auto;
			padding-right: vw(10);
			&-ttl {
				color: colors(col-blue);
				font-size: vw(12);
				font-weight: 700;
				line-height: 1;
			}
		}
		&-list {
			@extend .review-spec-list;
			:last-child {
				margin-bottom: 0;
			}
			&-ttl {
				@extend .review-spec-list-ttl;
				width: vw(90);
				color: colors(basetxt);
				font-weight: normal;
			}
			&-body {
				@extend .review-spec-list-body;
				line-height: 1;
			}
		}
	}
	&-detail {
		&-box {
			@extend .review-spec-box;
			margin-top: vw(10);
			padding-top: vw(10);
			border-top: vw(1) dotted colors(col-lightgray);
		}
	}
	&-photo {
		&-box {
			@include inline-block;
			width: 40%;
			padding-right: vw(10);
			vertical-align: middle;
			box-sizing: border-box;
		}
	}
	&-assessment-price {
		&-box {
			@include inline-block;
			width: 68%;
			padding-right: vw(10);
			box-sizing: border-box;
		}
	}
	// 口コミ概要
	//------------------------------------------------------------
	&-outline-block {
		// .mv-review-outline-block, .mv-shop-outline-messageにextendしているので、編集する際は継承先にも注意する
		clear: both;
		margin-top: vw(10);
		padding: vw(5) vw(9);
		border: vw(1) dotted colors(col-lightgray);
		& .ellipsis {
			display: -webkit-box;
			overflow : hidden;
			text-overflow: ellipsis;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
		}
	}
	// 口コミ詳細移動リンク
	//------------------------------------------------------------
	&-detail-link-txt {
		// .contents-index-list-body-linkにextendしているので、編集する際は継承先にも注意する
		display: inline-block;
		float: right;
		clear: both;
		margin-top: vw(10);
		padding: vw(5);
		background-color: colors(link);
		color: colors(col-white);
		.ico-arrow {
			color: colors(col-white);
		}
		:visited & {
			background-color: colors(visited);
		}
	}
}

//=========================
// 相場シミュレーター
//=========================

.market-simulate {
	&-item {
		&-block {
			font-size: 0;
		}
		&-box {
			@include inline-block;
			width: 50%;
			margin-bottom: vw(10);
			padding-right: vw(10);
			font-size: vw(14);
			box-sizing: border-box;
			&:nth-child(2n) {
				padding-right: 0;
			}
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
			}
			&-body {
				select,
				input[type="text"] {
					width: 100%;
					font-size: vw(14);
				}
			}
		}
	}
	&-search-btn {
		&-box {
			text-align: center;
			.btn-blue {
				padding-right: vw(40);
				padding-left: vw(40);
			}
		}
	}
	&-result {
		&-block {
			margin-top: vw(20);
			font-size: vw(14);
			.module-before-txt {
				font-weight: 700;
			}
			.assessment-module {
				&-block {
					margin-top: 0;
				}
				&-body {
				}
			}
		}
		&-tbl {
			width: 100%;
			margin-bottom: vw(10);
			font-size: vw(12);
			caption {
				color: colors(col-blue);
				font-size: vw(14);
				font-weight: 700;
				text-align: center;
			}
			td, th {
				padding: vw(5);
				border: vw(1) solid colors(col-lightgray);
				text-align: center;
				vertical-align: middle;
				&:first-child {
					width: 20%;
					text-align: left;
				}
			}
			th {
				background-color: colors(col-blue);
				color: colors(col-white);
			}
		}
	}
}

//------------------------------------------------------------
// 口コミ
//------------------------------------------------------------

.mv-review {
	&-list-area {
		// .mv-shop-outline-areaにextendしているので、編集する際は継承先にも注意する
		margin-bottom: vw(10);
		padding: vw(10);
		border: vw(1) solid colors(col-lightgray);
		background-color: colors(col-white);
		font-size: vw(12);
	}
	// 口コミサブ情報ブロック
	//------------------------------------------------------------
	&-supplement-info-block {
		@extend .review-supplement-info-block;
		font-size: vw(12);
	}
	&-user-name-box {
		@extend .review-user-name-box;
		width: auto;
	}
	&-days-box {
		@extend .review-days-box;
	}
	// 口コミメイン情報ブロック
	//------------------------------------------------------------
	&-main-info-block {
		@extend .review-main-info-block;
	}
	&-spec {
		&-box {
			@extend .review-spec-box;
			width: 100%;
			margin-bottom: vw(10);
		}
		&-list {
			@extend .review-spec-list;
			&.satisfy {
				width: 100%;
				clear: both;
			}
			&-ttl {
				@extend .review-spec-list-ttl;
				width: vw(60);
				.satisfy & {
					width: auto;
					padding: vw(2) vw(5);
					background-color: colors(col-red);
					color: colors(col-white);
					font-size: vw(12);
					line-height: 1;
					text-align: center;
					vertical-align: middle;
					white-space: nowrap;
				}
			}
			&-body {
				@extend .review-spec-list-body;
				padding-left: vw(10);
				.satisfy & {
					padding-left: 0;
					font-size: vw(16);
					line-height: 1;
					.review-star {
						font-size: vw(22);
					}
				}
				.review-star {
					@extend %review-star;
					margin-right: vw(3);
					font-size: vw(18);
					letter-spacing: vw(-5);
				}
			}
		}
	}
	&-sell-price {
		&-box {
			@extend .review-sell-price-box;
			display: table;
			width: 100%;
			margin-bottom: vw(10);
			padding-right: 0;
			&-left {
				@include table-cell;
				width: 40%;
				padding-right: vw(10);
				vertical-align: middle;
				box-sizing: border-box;
			}
			&-right {
				@include table-cell;
				width: 60%;
				vertical-align: middle;
				box-sizing: border-box;
			}
		}
		&-list {
			display: block;
			width: 100%;
			clear: both;
			&-ttl {
				@extend .review-sell-price-list-ttl;
				font-size: vw(14);
				color: colors(ttl);
			}
			&-body {
				@extend .review-sell-price-list-body;
			}
		}
	}
	&-eval {
		&-box {
			margin-bottom: vw(10);
			&-ttl {
				color: colors(ttl);
				font-size: vw(14);
				font-weight: 700;
			}
			&-body {
				.price-group {
					margin-bottom: vw(10);
					&-tbl {
						width: 100%;
						border: vw(1) solid colors(col-blue);
						border-bottom: none;
						tr {
							@include flex-block;
							-webkit-flex-wrap: wrap;
							flex-wrap: wrap;
						}
						td {
							display: table;
							width: 50%;
							border-right: vw(1) solid colors(col-blue);
							border-bottom: vw(1) solid colors(col-blue);
							vertical-align: middle;
							box-sizing: border-box;
							&:nth-child(2n) {
								border-right: none;
							}
							.ico-rank {
								@include table-cell;
								width: vw(20);
								padding: 0 vw(5);
							}
							.txt-price {
								@include table-cell;
								padding: 0 vw(5);
								color: colors(col-red);
								font-weight: 700;
							}
						}
					}
				}
				.difference-group {
					font-size: vw(14);
					text-align: center;
				}
			}
		}
	}
	// 口コミ概要
	//------------------------------------------------------------
	&-outline-block {
		@extend .review-outline-block;
		margin-top: 0;
	}
	// 売却ポイント
	//------------------------------------------------------------
	&-sell-point-block {
		font-size: vw(12);
	}
}

