//------------------------------------------------------------
// 査定会社TOP
//------------------------------------------------------------

//=========================
// 査定会社名がならぶブロック
//=========================

// インデックス
//------------------------------------------------------------

.index {
	&-area {
		@include flex-block;
		-webkit-flex-wrap: wrap;
		flex-wrap: wrap;
		-webkit-justify-content: flex-start;
		justify-content: flex-start;
		margin: 0 0 vw(10);
		text-align: center;
	}
	&-list {
		line-height: 1;
		width: 16%;
		@include margin(vw(3) vw(2) vw(3) null);
		&:nth-child(6n) {
			margin-right: 0;
		}
		& a, span {
			@include inline-block;
			width: vw(44);
			height: vw(44);
			border-radius: vw(3);
			line-height: vw(44);
			text-decoration: none;
			box-sizing: border-box;
		}
		& a {
			background-color: colors(link);
			color: colors(col-white);
		}
		& span {
			background-color: colors(col-xlightgray);
		}
	}
}

// 査定会社リスト
//------------------------------------------------------------

.merchant-name {
	&-area {
		margin-bottom: vw(10);
	}
	&-list {
		border: vw(1) solid colors(link);
		border-bottom: none;
		&:last-child {
			border-bottom: vw(1) solid colors(link);
		}
		a {
			display: block;
			text-decoration: none;
			&:visited .ico-arrow {
				color: colors(visited);
			}
		}
		figure {
			@include flex-block;
			position: relative;
			-webkit-align-items: center;
			align-items: center;
			width: 100%;
			padding: vw(7) vw(20) vw(7) vw(5);
			box-sizing: border-box;
			img:not(.ico-rank) {
				@include inline-block;
				width: vw(88);
				height: auto;
				margin-right: vw(5);
			}
			img.ico-rank {
				@include inline-block;
				width: vw(27);
				height: vw(38);
				margin-right: vw(5);
			}
			.txt-rank {
				@include inline-block;
				// width: calc(100% - vw(125));
				line-height: 1.25;
				.merchant-name {
					display: block;
				}
				.merchant-eval {
					display: block;
					font-size: vw(12);
					line-height: 1;
				}
				.review-star {
					@extend %review-star;
					position: relative;
					top: vw(2);
					font-size: vw(16);
				}
			}
			.ico-arrow {
				position: absolute;
				top: 50%;
				right: vw(5);
				color: colors(link);
				@include translate(0, -50%);
			}
		}
	}
}

//------------------------------------------------------------
// 査定会社詳細
//------------------------------------------------------------

//=========================
// メインビジュアル部分
//=========================

// 構成要素はmv-review-list-areaをほぼ継承

.mv-shop-outline {
	&-area {
		@extend .mv-review-list-area;
	}
	&-block {
		@extend .mv-review-main-info-block;
	}
	&-score-box {
		@extend .mv-review-spec-box;
		font-size: 0;
		figure {
			@include inline-block;
			position: relative;
			width: vw(80);
			height: vw(40);
			padding: vw(5);
			border: vw(1) solid colors(col-lightgray);
			font-size: vw(10);
			vertical-align: top;
			box-sizing: border-box;
			overflow: hidden;
			img {
				position: relative;
				top: 50%;
				left: 50%;
				width: auto;
				max-width: 100%;
				height: auto;
				max-height: 100%;
				@include translate(-50%,-50%);
			}
		}
	}
	&-group {
		@include inline-block;
		margin-left: vw(5);
		font-size: vw(12);
		vertical-align: top;
	}
	&-ttl {
		margin-bottom: vw(5);
		color: colors(ttl);
		font-size: vw(14);
		font-weight: 700;
		.review-num {
			color: colors(basetxt);
			font-size: vw(12);
			font-weight: normal;
		}
	}
	&-list {
		@extend .review-spec-list;
		// .mv-shop-outline-list.satisfy はextend元と同じなので省略
		&-ttl {
			@extend .mv-review-spec-list-ttl;
			width: vw(95);
			line-height: 1.25;
			// .satisfy .mv-shop-outline-list-ttl はextend元と同じなので省略
		}
		&-body {
			@extend .mv-review-spec-list-body;
			font-size: vw(14);
			padding-left: 0;
			line-height: 1.25;
			.satisfy & {
				padding-left: vw(20);
			}
			// .mv-shop-outline-list-body .review-star はextend元と同じなので省略
		}
	}
	// 査定会社からのメッセージ
	//------------------------------------------------------------
	&-message {
		@extend .review-outline-block;
		display: block;
		clear: both;
		margin-top: vw(10);
		font-size: vw(12);
		.message-ttl {
			font-weight: 700;
		}
	}
	&-info {
		&-box {
			@extend .mv-review-spec-box;
			margin-bottom: 0;
			box-sizing: border-box;
		}
		&-tbl {
			width: 100%;
			tr {
				border-top: vw(1) dotted colors(col-lightgray);
			}
			th, td {
				padding-top: vw(10);
				padding-bottom: vw(10);
			}
			th {
				width: vw(80);
				color: colors(col-blue);
				font-weight: 700;
			}
			td {
				padding-left: vw(10);
				padding-right: vw(10);
			}
		}
	}
}

//=========================
// 査定会社写真とキャプション部分
//=========================

.shop-main-photo {
	&-area {
		margin-bottom: vw(10);
		&-inner {
			@include flex-block;
			-webkit-align-items: stretch;
			align-items: stretch;
		}
	}
	&-block {
		@include flex-block;
		width: 100%;
		padding: vw(10);
		border: vw(1) solid colors(col-xlightgray);
		font-size: vw(10);
		@include box-shadow(0,vw(2),vw(2),0,rgba(colors(col-black),0.2));
		box-sizing: border-box;
		table-layout: fixed;
		figure {
			@include inline-block;
			position: relative;
			width: vw(80);
			height: vw(60);
			border: vw(1) solid colors(col-lightgray);
			box-sizing: border-box;
			vertical-align: top;
			overflow: hidden;
			img {
				position: relative;
				top: 50%;
				left: 50%;
				max-height: 100%;
				@include translate(-50%,-50%);
			}
		}
	}
	&-captxt {
		@include inline-block;
		width: calc(100% - #{vw(90)});
		margin-left: vw(10);
		vertical-align: top;
	}
}

//=========================
// 査定会社のメインテキスト
//=========================

.shop-main-message {
	&-ttl {
		font-weight: 700;
	}
}

//=========================
// 買取強化車種
//=========================

.actively-purchase {
	&-area {
		@extend .site-menu-area;
		border-radius: vw(3);
		.car-thumb-area {
			margin-bottom: 0;
		}
	}
	&-ttl {
		@extend .site-menu-ttl;
		font-size: vw(16);
	}
}

