//------------------------------------------------------------
// ガイドTOP
//------------------------------------------------------------

.contents-index-list {
	display: table;
	width: 100%;
	margin-bottom: vw(10);
	padding-bottom: vw(10);
	border-bottom: vw(1) dotted colors(col-lightgray);
	text-decoration: none;
	table-layout: fixed;
	&:last-child {
		margin-bottom: 0;
		padding-bottom: 0;
		border-bottom: none;
	}
	&-img {
		@include table-cell;
		width: vw(100);
		vertical-align: top;
		img {
			border: vw(1) solid colors(link);
			:visited & {
				border: vw(1) solid colors(visited);
			}
		}
	}
	&-body {
		@include table-cell;
		@include padding(null null null vw(10));
		vertical-align: top;
		&-ttl {
			display: block;
			color: colors(ttl);
			font-weight: 700;
			.qa-type & {
				display: table;
				&:before {
					@include table-cell;
					width: 1em;
					vertical-align: top;
					content: "Q.";
				}
			}
		}
		&-txt {
			display: block;
			color: colors(basetxt);
			font-size: vw(12);
			.qa-type & {
				display: table;
				&:before {
					@include table-cell;
					width: 1em;
					vertical-align: top;
					content: "A.";
				}
			}
		}
		&-link {
			@extend .review-detail-link-txt;
		}
	}
}

//------------------------------------------------------------
// 初心者ガイドTOP
//------------------------------------------------------------

//=========================
// 査定・売却のポイント
//=========================

.text-with-arw-list {
	@extend .merchant-name-list;
	.no-link {
		display: block;
		text-decoration: none;
		box-sizing: border-box;
	}
}

//------------------------------------------------------------
// 用語集TOP
//------------------------------------------------------------

// 基本的にサイトマップと同じ構成です


//------------------------------------------------------------
// 用語集詳細
//------------------------------------------------------------

//=========================
// 各カテゴリの用語一覧
//=========================

.glossary-category {
	&-list {
		@extend .ico-arw-wedge;
		display: block;
		margin-bottom: vw(15);
		&:last-child {
			margin-bottom: 0;
		}
	}
}

//=========================
// 自動車税還付の流れ
//=========================

.under-arw-list {
	&-area {
		margin-bottom: vw(20);
		counter-reset: under-arw-list;
		li:not(:last-child) {
			position: relative;
			padding-bottom: vw(50);
			&:before {
				position: absolute;
				bottom: vw(25);
				left: 50%;
				width: vw(40);
				height: vw(15);
				background-color: colors(col-gray);
				line-height: 1;
				@include translate(-50%,0);
				content: "";
			}
			&:after {
				position: absolute;
				bottom: vw(10);
				left: 50%;
				border: vw(40) solid transparent;
				border-top: vw(15) solid colors(col-gray);
				border-bottom: 0 solid transparent;
				@include translate(-50%,0);
				content: "";
			}
		}
		.list-content-inner {
			display: block;
			position: relative;
			@include padding(vw(10) vw(10) vw(10) vw(25));
			border: vw(1) solid colors(col-lightgray);
			&:before {
				position: absolute;
				left: vw(10);
				top: vw(10);
				counter-increment: under-arw-list;
				content: counter(under-arw-list);
			}
		}
	}
}

//=========================
// 用語説明のブロック
//=========================

.glossary-detail {
	&-area {
		&:not(:first-child) {
			padding-top: vw(30);
			border-top: vw(1) dotted colors(col-lightgray);
			p {
				&:not(:last-of-type) {
					margin-bottom: vw(20);
				}
			}
		}
	}
}

//------------------------------------------------------------
// よくある質問TOP
//------------------------------------------------------------

//=========================
// カテゴリインデックス
//=========================

// 査定・売却のポイントと同じです

//=========================
// 各カテゴリ一覧
//=========================

.faq-category {
	&-list {
		&:first-child a {
			border-top: vw(1) solid colors(link);
		}
		a {
			@extend .ico-arw-wedge;
			display: table;
			position: relative;
			width: 100%;
			padding: vw(7) vw(20) vw(7) vw(5);
			border: vw(1) solid colors(link);
			border-top: none;
			text-decoration:  none;
			box-sizing: border-box;
			&:before {
				@include table-cell;
				width: 1em;
				vertical-align: top;
				content: "Q.";
			}
			&:after {
				top: 50%;
				left: auto;
				right: vw(10);
				border-color: colors(link);
				-moz-transform: rotate(45deg) translateX(0%) translateY(-50%);
				-webkit-transform: rotate(45deg) translateX(0%) translateY(-50%);
				-o-transform: rotate(45deg) translateX(0%) translateY(-50%);
				-ms-transform: rotate(45deg) translateX(0%) translateY(-50%);
				transform: rotate(45deg) translateX(0%) translateY(-50%);
			}
			&:visited:after {
				border-color: colors(visited);
			}
		}
	}
}

//------------------------------------------------------------
// よくある質問詳細
//------------------------------------------------------------

.faq-detail {
	&-area {
		&:not(:first-child) {
			padding-top: vw(20);
			border-top: vw(1) dotted colors(col-lightgray);
		}
		p {
			&:not(:last-of-type) {
				margin-bottom: vw(20);
			}
		}
	}
	&-question {
		position: relative;
		margin-bottom: vw(10);
		@include padding(vw(5) null null vw(40));
		color: colors(ttl);
		font-weight: 700;
		&:before {
			@include inline-block;
			position: absolute;
			left: 0;
			top: 0;
			width: vw(30);
			height: vw(30);
			@include border-radius(50%,50%,50%,50%);
			background-color: colors(ttl);
			color: colors(col-white);
			font-size: vw(20);
			font-weight: 700;
			line-height: vw(30);
			text-align: center;
			content: "Q";
		}
	}
	&-answer {
		position: relative;
		margin-bottom: vw(10);
		@include padding(vw(5) null null vw(40));
		&:before {
			@include inline-block;
			position: absolute;
			left: 0;
			top: 0;
			width: vw(30);
			height: vw(30);
			@include border-radius(50%,50%,50%,50%);
			background-color: colors(col-gray);
			color: colors(col-white);
			font-size: vw(20);
			font-weight: 700;
			line-height: vw(30);
			text-align: center;
			vertical-align: middle;
			content: "A";
		}
	}
}

//------------------------------------------------------------
// 廃車ガイドTOP
//------------------------------------------------------------

//=========================
// チェックリスト
//=========================

.type-checkbox {
	&-area {
		margin-bottom: vw(20);
	}
	&-list {
		position: relative;
		margin-bottom: vw(10);
		padding-left: vw(26);
		&:before {
			position: absolute;
			top: vw(2);
			left: 0;
			width: vw(20);
			height: vw(20);
			border: vw(2) solid colors(disable);
			background-color: colors(col-white);
			content: "";
		}
		&:after {
			position: absolute;
			top: vw(6);
			left: vw(3);
			width: vw(14);
			height: vw(6);
			border-left: vw(4) solid colors(link);
			border-bottom: vw(4) solid colors(link);
			@include rotate(-45);
			content: "";
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
}