//共通コンポーネントでスマホ用に上書き必要なものをここ記載してください。

//------------------------------------------------------------
// _base
//------------------------------------------------------------

body {
	font-size: vw(16);
	overflow-x: hidden;
}

img {
	width: auto;
	max-width: 100%;
	height: auto;
}

input, textarea, select {
	font-size: vw(16);
}

a {
	&:hover {
		@include opacity(1);
	}
}

input {
	&[type="radio"], &[type="checkbox"] {
		& + label {
			padding: vw(3) 0 vw(3) vw(26);
			&:before {
				top: vw(4);
				width: vw(20);
				height: vw(20);
				border: vw(2) solid colors(col-xlightgray);
			}
		}
		&:checked + label {
			&:before {
				border: vw(2) solid colors(disable);
			}
		}
	}
	&[type='radio'] {
		& + label {
			&:after {
				top: vw(10);
				left: vw(6);
				width: vw(12);
				height: vw(12);
			}
		}
		&:checked + label {
			&:after {
				@include box-shadow(vw(1),vw(1),vw(2),0,rgba(0,0,0,0.45),inset);
			}
		}
	}
	&[type='checkbox'] {
		& + label {
			&:after {
				top: vw(8);
				left: vw(3);
				width: vw(14);
				height: vw(6);
				border-left: vw(4) solid rgba(colors(col-xlightgray),0.5);
				border-bottom: vw(4) solid rgba(colors(col-xlightgray),0.5);
			}
		}
		&:checked + label {
			&:after {
				border-left: vw(4) solid colors(link);
				border-bottom: vw(4) solid colors(link);
				@include box-shadow(vw(-1),vw(1),vw(1),0,rgba(0,0,0,0.45));
			}
		}
	}

	&[type="text"], &[type="tel"], &[type="email"], &[type="password"], &[disabled] {
		padding: vw(3);
		height: vw(26);
		border: vw(1) solid colors(col-lightgray);
		// @include border-radius(vw(3),vw(3),vw(3),vw(3));
		// @include box-shadow(vw(1),vw(1),vw(1),0, rgba(0,0,0,0.25),inset);
		&:focus {
			@include box-shadow(0,0,vw(5),0,rgba(colors(link),0.75));
		}
		@include break-point(tblandscape) {
			height: vw(34);
		}
		@include break-point(tbportrait) {
			height: vw(34);
		}
		@include break-point(mobile) {
			height: vw(34);
		}
	}

	&[disabled] {
		@include box-shadow(vw(1),vw(1),0,0,rgba(0,0,0,0),inset);
	}

	&[readonly] {
		@include box-shadow(vw(1),vw(1),0,0,rgba(0,0,0,0),inset);
		&:focus {
			@include box-shadow(0,0,0,0,rgba(colors(link),0.0));
		}
	}
}

textarea {
	padding: vw(3);
	border: vw(1) solid colors(col-lightgray);
	@include box-shadow(vw(1),vw(1),vw(1),0, rgba(0,0,0,0.25),inset);
	&:focus {
		@include box-shadow(0,0,vw(5),0,rgba(66,187,255,0.8));
	}
}

select {
	height: vw(26);
	border: vw(1) solid colors(col-lightgray);
	&:focus {
		@include box-shadow(0,0,vw(5),0,rgba(66,187,255,0.8));
	}
	@include break-point(tblandscape) {
		height: vw(34);
	}
	@include break-point(mobile) {
		height: vw(34);
	}
}

//------------------------------------------------------------
// title
//------------------------------------------------------------

.ttl {
	&-page {
		width: $spContentsWidth;
		margin-bottom: vw(10);
		padding: vw(10);
		border-top: vw(1) solid colors(ttl);
		border-bottom: vw(1) solid colors(ttl);
		font-size: vw(20);
		line-height: 1.5;
	}
	&-section {
		top: -6px;
		margin-bottom: vw(5);
		padding-bottom: vw(3);
		border-bottom: vw(3) solid colors(col-xlightgray);
		font-size: vw(16);
		& .section-word {
			top: vw(6);
			padding-bottom: vw(3);
			@include padding(null vw(4) vw(3) vw(1));
			border-bottom: vw(3) solid colors(ttl);
		}
		& .updated-date {
			bottom: vw(8);
			font-size: vw(12);
		}
		+ .updated-date {
			margin-top: vw(-5);
			margin-bottom: vw(10);
			font-size: vw(12);
			line-height: 1;
			text-align: right;
		}
	}
	&-block {
		margin-bottom: vw(5);
		font-size: vw(14);
	}
	&-make {
		margin-bottom: vw(10);
		font-size: vw(20);
		.make-img {
			@include inline-block;
			width: vw(36);
			height: vw(28);
			margin-right: vw(5);
		}
	}
	&-column-img {
		@include inline-block;
		left: 50%;
		@include translate(-50%, 0);
		.news-page & {
			border: vw(1) solid colors(col-lightgray);
		}
		.column-img-txt {
			left: vw(10);
			font-size: vw(12);
		}
	}
}

//------------------------------------------------------------
// breadcrumbs
//------------------------------------------------------------

.breadcrumbs {
	&-area {
		width: $spContentsWidth;
		margin: 0;
		padding: 0 vw(10);
		box-sizing: border-box;
	}
	&-list {
		margin: 0 vw(5) vw(10) 0;
		font-size: vw(10);
		line-height: 2;
		&:first-child {
			font-size: vw(16);
			line-height: 1;
		}
		&:after {
			top: vw(6);
		}
	}
}

//------------------------------------------------------------
// icons
//------------------------------------------------------------

.ico-external {
	width: 1em;
}

//------------------------------------------------------------
// Buttons
//------------------------------------------------------------

.btn {
	&-blue {
		padding: vw(8);
		@include border-radius(vw(3),vw(3),vw(3),vw(3));
		text-shadow: vw(-1) vw(-1) 0 rgba(colors(col-black), 0.4);
		@include box-shadow(0,vw(5),0,0,rgba(colors(col-black),0.2));
		&:hover {
			top: vw(1);
			@include box-shadow(0,vw(4),0,0,rgba(colors(col-black),0.2));
		}
		&:active {
			top: vw(5);
			@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
		}
	}
	&-gray {
		padding: vw(8);
		@include border-radius(vw(3),vw(3),vw(3),vw(3));
		@include box-shadow(0,vw(5),0,0,rgba(colors(col-black),0.2));
		&:hover {
			top: vw(1);
			@include box-shadow(0,vw(4),0,0,rgba(colors(col-black),0.2));
		}
		&:active {
			top: vw(5);
			@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
		}
	}
	&-cv {
		padding: vw(10) vw(8);
		@include border-radius(vw(3),vw(3),vw(3),vw(3));
		text-shadow: vw(1) vw(1) vw(1) rgba(0,0,0,0.4);
		@include box-shadow(0,vw(5),0,0,rgba(colors(col-black),0.2));
		&:hover {
			top: vw(1);
			@include box-shadow(0,vw(4),0,0,rgba(colors(col-black),0.2));
		}
		&:active {
			top: vw(5);
			@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
		}
		.ico-free {
			left: vw(5);
			width: vw(40);
			height: vw(30);
			padding-top: vw(9);
			font-size: vw(14);
			@include box-shadow(vw(1),vw(1),vw(1),0,rgba(0,0,0,0.4));
		}
		.cv-btn-txt {
			margin: 0 0 vw(5) vw(30);
			font-size: vw(16);
		}
		.cv-steps-flow-box {
			margin-left: vw(24);
			font-size: vw(10);
			white-space: nowrap;
			@include scale(0.8,0.8);
			.cv-steps-flow-cell {
				padding: vw(2) vw(2) vw(2) vw(14);
				&:before, &:after {
					margin-top: vw(-7);
					border-width: vw(7);
					border-right: 0;
				}
				&:before {
					right: vw(-8);
				}
				&:after {
					right: vw(-10);
				}
				&:first-child {
					padding-left: vw(5);
					&:before {
						border-width: vw(7);
						border-right: 0;
					}
				}
			}
		}
		.ico-arrow {
			right: vw(-10);
			margin-top: vw(-20);
			font-size: vw(40);
		}
	}
	&-pagetop {
		@include border-radius(vw(3),vw(3),vw(3),vw(3));
		padding: vw(10) vw(6) vw(1);
		font-size: vw(12);
		line-height: 1.75;
		.ico-arrow {
			margin: vw(-10) 0;
		}
	}
}

//------------------------------------------------------------
// Typography
//------------------------------------------------------------

@for $value from 1 through 30 {
	.fsize-#{$value * 2} {
		font-size: vw(2 * $value);
	}
}

//------------------------------------------------------------
// modal
//------------------------------------------------------------

.ybox {
	&-container {
		border: vw(3) solid colors(col-black);
		border-radius: vw(3);
	}
	&-close {
		padding: 0 vw(2.5) vw(5) vw(5);
		border-left: vw(2) solid colors(col-white);
		border-bottom: vw(2) solid colors(col-white);
		font-size: vw(28);
	}
}

.modal-content-area {
	padding: vw(10);
}

.zip-advanced-table {
	tr, tbody {
		display: block;
	}
	th {
		display: block;
		width: auto;
		padding-top: vw(5);
	}
	td {
		display: block;
		padding-top: vw(5);
		select {
			display: block;
			width: 100%;
			margin-bottom: vw(5);
		}
	}
}

.zip-submit-btn-block {
	margin: vw(10) 0;
	&.btn-blue, .btn-gray {
		margin: 0 vw(10);
	}
}

//------------------------------------------------------------
// icons
//------------------------------------------------------------

.ico-new {
	@include inline-block;
	margin: 0 0 0 vw(2);
	padding: vw(2) vw(5);
	font-size: vw(10);
}

.ico-external {
	@include margin(null vw(5) null vw(5));
}

.ico-arw-wedge {
	padding-right: vw(20);
}