//------------------------------------------------------------
// 買取TOP
//------------------------------------------------------------

//=========================
// コンテンツ内挿絵
//=========================

.main-content-atrwork {
	text-align: center;
	@include margin(vw(10) null vw(10) null);
}

// コンテンツ内強調背景
//------------------------------------------------------------

.content-strong-bg {
	background-color: colors(col-lightyellow);
	&.atrwork-cap {
		margin-top: vw(10);
		padding: vw(5);
		font-size: vw(16);
		text-align: center;
	}
}

//=========================
// コンテンツ内テーブル
//=========================

.main-content-tbl {
	width: 100%;
	margin-bottom: vw(20);
	border-top: vw(1) dotted colors(col-lightgray);
	border-left: vw(1) dotted colors(col-lightgray);
	// 基本設定
	th, td {
		padding: vw(10);
		border-right: vw(1) dotted colors(col-lightgray);
		border-bottom: vw(1) dotted colors(col-lightgray);
		vertical-align: top;
		box-sizing: border-box;
		&:not(.content-strong-bg){
			background-color: colors(col-white);
		}
	}
	th {
		text-align: center;
	}
	thead {
		th, td {
			vertical-align: middle;
			img {
				display: block;
				width: 50%;
				margin: 0 auto;
			}
		}
	}
	tbody {
		th {
			img {
				display: block;
				width: 50%;
				margin: 0 auto;
			}
		}
	}
	// 項目多数横並び構成
	&.horizon-long-tbl {
		display: table;
		width: 100%;
		table-layout: fixed;
		th, td {
			display: block;
			font-size: vw(14);
		}
		tr {
			@include inline-block;
			vertical-align: top;
			white-space: normal;
		}
		thead {
			@include table-cell;
			width: vw(100);
			vertical-align: top;
			tr {
				display: block;
			}
		}
		tbody {
			@include table-cell;
			vertical-align: top;
			overflow-x: auto;
			white-space: nowrap;
			font-size: 0;
			th, td {
				width: vw(170);
			}
		}
	}
	// メリデメ・得意分野など説明が多いで使用
	&.long-description-tbl {
		th {
			font-size: vw(12);
		}
		td {
			font-size: vw(10);
		}
	}
	// 2カラム構成
	&.two-column {
		th, td {
			width: 50%;
			&:first-child {
				width: auto;
			}
		}
		// 項目名と内容が対になる2カラム
		&.pair {
			th, td {
				&:not(:first-child) {
					width: 72%;
				}
			}
		}
	}
	// 3カラム構成
	&.three-column {
		th, td {
			width: 33%;
			&:first-child {
				width: auto;
			}
		}
		// メリット・デメリットなど比較したい3カラム
		&.compare {
			th, td {
				&:not(:first-child) {
					width: 36%;
				}
			}
		}
	}
	// 運輸局一覧用
	&.transportation-list {
		thead {
			th {
				color: colors(col-blue);
			}
		}
		tbody {
			th {
				width: 25%;
			}
			td {
				&:last-child {
					width: 20%;
					text-align: center;
				}
			}
		}
	}
}

//=========================
// 買取店の種類説明分
//=========================

.purchase-explain-txt {
	display: block;
	margin-bottom: vw(10);
	vertical-align: top;
	box-sizing: border-box;
}

// ここが得意
//------------------------------------------------------------

.specialty-point {
	display: block;
	padding: vw(5) vw(10);
	border-radius: vw(3);
	border: vw(1) solid colors(col-blue);
	vertical-align: top;
	box-sizing: border-box;
	&-ttl {
		color: colors(ttl);
		font-weight: 700;
		text-align: left;
		img {
			@include inline-block;
			width: 15%;
			@include margin(0 vw(5) 0 0);
		}
	}
	&-body {
		text-align: left;
	}
}

//=========================
// 安心ポイント
//=========================

.blue-disc-num {
	&-area {
		counter-reset: blue-disc-num;
	}
	&-list {
		position: relative;
		@include margin(null null vw(20) vw(50));
		font-size: vw(14);
		font-weight: 700;
		&:last-child {
			margin-bottom: 0;
		}
		&:before {
			@include inline-block;
			position: absolute;
			top: 0;
			left: vw(-40);
			width: vw(30);
			height: vw(30);
			@include border-radius(50%,50%,50%,50%);
			background-color: colors(ttl);
			color: colors(col-white);
			font-size: vw(20);
			font-weight: 700;
			line-height: vw(30);
			text-align: center;
			vertical-align: middle;
			counter-increment: blue-disc-num;
			content: counter(blue-disc-num);
		}
	}
}

//=========================
// ピックアップ口コミ
//=========================

.pickup-review {
	&-area {
		position: relative;
		margin-bottom: vw(20);
	}
	&-block {
		position: relative;
	}
	&-list {
		padding: vw(10);
		border: vw(2) solid colors(col-yellow);
		border-bottom: none;
		box-sizing: border-box;
		&:last-child {
			border-bottom: vw(2) solid colors(col-yellow);
		}
		&-ttl {
			@extend .assessment-example-car-ttl;
			position: static;
			left: 0;
			width: auto;
			@include translate(0%,0%);
		}
		&-body {
			display: table;
			width: 100%;
			table-layout: fixed;
			figure {
				@include table-cell;
				width: 45%;
				text-align: center;
				vertical-align: middle;
				img {
					position: relative;
					width: auto;
					max-width: 100%;
					height: auto;
					max-height: 100%;
				}
			}
		}
		&-info {
			@include table-cell;
			vertical-align: top;
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
			}
			// ここで使ってる.example-car-info-groupは_block.scssに記載
		}
		&-eval {
			&-box {
				@extend .mv-review-eval-box;
				&-ttl {
					@extend .mv-review-eval-box-ttl;
				}
				&-body {
					@extend .mv-review-eval-box-body;
				}
				// ここで使ってる.price-group-tbl, .price-group-tbl, .difference-groupは_block.scssに記載
			}
		}
	}
}