//------------------------------------------------------------
// サイトマップ
//------------------------------------------------------------

.sitemap-tree {
	&-area {
		margin-bottom: vw(10);
	}
	&-list {
		border: vw(1) solid colors(link);
		border-bottom: none;
		&:last-child {
			border-bottom: vw(1) solid colors(link);
		}
		a {
			@extend .ico-arw-wedge;
			display: block;
			position: relative;
			padding: vw(10) vw(5);
			text-decoration: none;
			&:after {
				top: 50%;
				left: auto;
				right: vw(10);
				border-color: colors(link);
				transform: rotate(45deg) translateX(0%) translateY(-50%);
			}
			&:visited:after {
				border-color: colors(visited);
			}
		}
	}
}